<template>
  <div class="order-list">
    <div class="order-list__search">
      <OrderTab @orderTab="orderTab"></OrderTab>
      <el-button
        class="order-list__search__collapse-btn"
        type="text"
        @click="searchFormShow = !searchFormShow"
      >
        {{ searchFormShowText }}
        <i
          class="el-icon--right"
          :class="searchFormShow ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"
        />
      </el-button>
      <el-collapse-transition>
        <search-form
          :orderTab="orderTabVal"
          ref="orderSearchRef"
          v-show="searchFormShow"
          :cur-order-status="curTabName === 'all' ? '' : curTabName"
          :local-form-data="searchParams"
          @search="handleSearch"
          @orderBasicOpts="orderBasicOpts"
        />
      </el-collapse-transition>
    </div>
    <div class="order-list__body">
      <el-tabs v-model="curTabName" @tab-click="handleTabClick">
        <el-tab-pane
          v-for="{ label, name, num } in tabArr"
          :key="name"
          :name="name"
        >
          <el-badge slot="label" :value="num" :max="99" class="item">
            <el-button
              :class="
                name === 'pending' ? 'order-list__body__button--pending' : ''
              "
              type="text"
              size="small"
              >{{ label }}</el-button
            >
          </el-badge>
        </el-tab-pane>
      </el-tabs>
      <div v-if="loading" v-loading="loading" style="height:250px;"></div>
      <OrderTable
        v-if="!loading"
        :orderTab="orderTabVal"
        :currentPage="current_page"
        :pageSize="page_size"
        :total="total"
        :tableData="orderList"
        @search="handleSearch"
      ></OrderTable>
    </div>
  </div>
</template>
<script>
import {
  getOrderList as getOrderListApi,
  getOrderProcess as getOrderProcessApi
} from '@/api/order'

import OrderTab from './OrderTab'
import SearchForm from './SearchForm'
import OrderTable from './OrderTable'

import { deepCopy } from '@/utils'
import { RMBSymbol, fenToYuan } from '@/filters'

import { formatTime } from '@/utils/moment'
export default {
  components: { SearchForm, OrderTable, OrderTab },
  data() {
    return {
      searchParams: {},
      loading: false,
      orderTabVal: '',
      searchFormShow: true,
      curTabName: 'all',
      orderProcess: {},
      orderList: [],
      total: 0,
      page_size: 10,
      current_page: 1,
      orderStatusOpts: []
    }
  },
  computed: {
    refundReasons() {
      return [
        { key: 'misdiagnose', value: '误诊' },
        { key: 'user_asked', value: '用户要求退款' },
        { key: 'other', value: '其它' }
      ]
    },
    searchFormShowText() {
      return this.searchFormShow ? '收起' : '展开搜索条件'
    },
    tabArr() {
      return [
        {
          label: '全部',
          name: 'all'
        }
      ].concat(
        (this.orderStatusOpts || [])
          .filter(
            orderStatus =>
              [
                'pending',
                'after_sale',
                'wait_pay',
                'success',
                'closed',
                'refund_closed',
                'refund_refused',
                'refund_success'
              ].indexOf(orderStatus.value) > -1
          )
          .map(({ label, value }) => ({
            label: label,
            name: value,
            num: this.orderProcess[value] || ''
          }))
      )
    }
  },
  created() {
    this.handleSearch()
  },
  methods: {
    orderBasicOpts(val) {
      this.orderStatusOpts = val.order_status
    },
    orderTab(val) {
      this.orderTabVal = val
    },
    async getOrderProcess() {
      const { data } = await getOrderProcessApi()
      this.orderProcess = deepCopy(data)
    },
    async handleSearch(query) {
      this.loading = true
      if (query && query.searchParams) {
        this.searchParams = query.searchParams
      }
      try {
        let _query = Object.assign(
          {
            page_size:
              query && query.page_size ? query.page_size : this.page_size,
            current_page:
              query && query.current_page
                ? query.current_page
                : this.current_page
          },
          this.searchParams
        )
        _query = Object.assign(_query, _query.searchParams)
        if (_query.createdDateRange && _query.createdDateRange.length >= 2) {
          _query.create_time_begin = _query.createdDateRange[0]
          _query.create_time_end = _query.createdDateRange[1]
        } else {
          _query.create_time_begin = ''
          _query.create_time_end = ''
        }
        if (this.orderTabVal && this.orderTabVal !== '0') {
          _query = Object.assign(_query, {
            order_type: this.orderTabVal
          })
        }
        const { data } = await getOrderListApi(_query)
        this.loading = false
        this.setTableData({ ...data })
        await this.handleSearchAfter(_query.order_status)
      } catch (err) {
        this.loading = false
        console.error(err)
      }
    },
    async handleSearchAfter(orderStatus) {
      this.curTabName = orderStatus || 'all'
      await this.getOrderProcess()
    },
    async setTableData({ list, count, current_page, page_size }) {
      this.current_page = current_page
      this.page_size = page_size
      this.total = count
      this.orderList = list.map(order => ({
        afterSaleStatus:
          order.after_sale_status && order.after_sale_status.key
            ? order.after_sale_status.key
            : '',
        canRefund: order.can_refund,
        orderRefundId:
          order.order_refund && order.order_refund._id
            ? order.order_refund._id
            : '',
        unitPrice: order.unit_price,
        actualUnitPrice: RMBSymbol(fenToYuan(order.unit_price)),
        count: order.count,
        cost: RMBSymbol(fenToYuan(order.cost)),
        orderId: order.order_id,
        orderName: order.order_name,
        orderAction: order.order_action, //签约类型
        fd_status: order.fd_status, //服务状态
        single_ask_user: order.single_ask_user, //接诊人
        department: order.department, //服务科室
        orderType: order.order_type,
        createdDate: formatTime(order.create_at),
        tradeStatus: order.trade_status,
        userId: order.user_id,
        userName: order.nickname,
        weappScene: order.scene && order.scene.weapp ? order.scene.weapp : '',
        customScene:
          order.scene && order.scene.custom ? order.scene.custom : '',
        id: order._id
      }))
    },
    async handleTabClick() {
      await this.handleSearch({
        current_page: 1,
        page_size: 10,
        searchParams: { order_status: this.curTabName }
      })
    }
  }
}
</script>
<style lang="scss">
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';

.order-list {
  .el-badge__content {
    background: #f8747f;
    &.is-fixed {
      transform: translateY(-30%) translateX(100%) scale(0.7);
    }
  }
  .el-tabs {
    &__header {
      margin-bottom: 0;
    }
    &__nav-wrap {
      padding-left: 30px;
      &::after {
        height: 1px;
      }
    }
    &__item {
      padding: 5px 16px;
      height: 50px;
      &.is-active {
        .el-button--text {
          color: #{$activeColor};
        }
      }
      .el-button--text {
        font-size: 14px;
        color: #{$textLightBlock};
        &.order-list__body__button--pending {
          color: #f8747f;
        }
      }
      .el-badge {
        padding: 0 16px;
        &__content {
          background: #f8747f;
          &.is-fixed {
            transform: translateY(10%) translateX(50%) scale(0.7);
          }
        }
      }
    }
  }
}
</style>

<style lang="scss" scoped>
@import '~@/styles/variables.scss';

.order-list {
  .cherry {
    color: #f8747f;
  }
  &__search {
    position: relative;
    padding-bottom: 30px;
    &__collapse-btn {
      position: absolute;
      top: 8px;
      right: 0;
      z-index: 99;
    }
  }
  &__body {
    border: 1px solid #ebeef5;
    border-radius: 4px;
    &__pagination {
      padding: 60px 0 40px;
      text-align: center;
    }
    &__checkbox-hide {
      padding: 30px 20px 20px;
      color: #409eff;
    }
    &__table {
      &__trade-Status,
      &__source,
      &__user {
        display: flex;
        align-items: center;
        justify-content: center;
        line-height: 1;
        flex-direction: column;
      }
      &__trade-Status {
        min-height: 70px;
      }
      &__source__custom-scene,
      &__user__see-btn {
        padding-top: 5px;
        transform: scale(0.84);
      }
      &__user__see-btn {
        padding-bottom: 0;
        font-size: 14px;
        color: #{$textLightBlock};
      }
    }
  }
}
</style>
