<template>
  <el-tabs
    v-model="orderTab"
    @tab-click="handleTabClick"
    class="margin-bottom-30"
  >
    <el-tab-pane
      v-for="{ label, name } in tabArr"
      :label="label"
      :key="name"
      :name="name"
    ></el-tab-pane>
  </el-tabs>
</template>
<script>
export default {
  // props:{}
  data() {
    return {
      orderTab: '',
      tabArr: [
        {
          label: '全部',
          name: ''
        },
        {
          label: '家庭医生',
          name: 'FdServiceType'
        },
        {
          label: '单次问诊',
          name: 'InquiryServiceType'
        }
      ]
    }
  },
  methods: {
    handleTabClick() {
      this.$emit('orderTab', this.orderTab)
    }
  }
}
</script>
