<template>
  <MyTable
    size="mini"
    :stripe="false"
    :border="false"
    :multiple="false"
    :outlineBorder="false"
    :emptyText="emptyText"
    :operation="operation"
    :column="column"
    :data="tableData"
    :currentPage="currentPage"
    :pageSize="pageSize"
    :total="total"
    @handleSizeChange="handleSizeChange"
    @handleCurrentChange="handleCurrentChange"
  >
    <template v-slot:userInfo="scope">
      <div>
        <div style="line-height: 20px">{{ scope.row.userName }}</div>
        <span>{{ scope.row.userId }}</span>
        <el-button
          type="text"
          size="mini"
          @click.stop.prevent="seeUserOrder(scope.row.userId)"
        >
          <svg-icon icon-class="tuanduicankaoxian" />
          查询该用户{{ orderTabName }}订单
        </el-button>
      </div>
    </template>
    <template v-slot:tradeStatus="scope">
      <div class="order-list__body__table__trade-Status">
        <p>{{ scope.row.tradeStatus | tradeStatusFilter }}</p>
        <el-button
          class="trade_detail_btn"
          type="text"
          size="mini"
          @click="seeDetail(scope.row.id)"
          >详情</el-button
        >
        <close-trade
          v-if="scope.row.tradeStatus === 'PENDING'"
          :id="scope.row.id"
          v-model="closeTrandVisible"
          @closeTradeAfter="search(scope.row)"
        >
          <el-button
            class="close-btn cherry"
            type="text"
            size="mini"
            @click="closeTrandVisible = true"
            >关闭交易</el-button
          >
        </close-trade>
      </div>
    </template>
    <template v-slot:afterSaleStatus="scope">
      <div class="order-list__body__table__source">
        <span v-if="!scope.row.afterSaleStatus || scope.row.canRefund">无</span>
        <el-button
          v-else
          type="text"
          @click="seeRefundDetail(scope.row.orderRefundId)"
          >{{ scope.row.afterSaleStatus | afterSaleStatusFilter }}</el-button
        >
        <apply-refund
          v-if="scope.row.canRefund"
          :id="selectOrder.id"
          v-model="dialogVisible"
          :refund-reasons="refundReasons"
          :order-type="selectOrder.orderType"
          @applyRefundAfter="applyRefundAfter(scope.row.userId)"
        >
          <el-button
            class="cherry"
            type="text"
            size="mini"
            @click="handlerRefundButtonClick(scope.row)"
            >退款给用户</el-button
          >
        </apply-refund>
      </div>
    </template>
    <template v-slot:scene="scope">
      <el-tooltip :content="scope.row.weappScene" placement="top">
        <p>
          {{ scope.row.weappScene.substring(0, 10) }}...{{
            scope.row.customScene
          }}
        </p>
      </el-tooltip>
    </template>
  </MyTable>
</template>
<script>
import { tradeStatusMixin } from '../mixins'
import ApplyRefund from '@/views/trade-manage/refund/components/ApplyRefund'
import { CloseTrade } from '../components'
import { afterSaleStatusFilter } from '../filters'
import { localSave } from '@/utils'

import { orderListTableColumn } from '../../tableColumn.js'
import MyTable from '@/components/MyTable'
export default {
  mixins: [tradeStatusMixin],
  data() {
    // 表格操作配置
    var operation = {
      nowPage: 'orderListTable',
      show: false
    }
    return {
      selectOrder: { id: '', orderType: '' },
      emptyText: '没有符合条件的订单，请尝试其他搜索条件',
      column: [],
      closeTrandVisible: false,
      dialogVisible: false,
      operation: operation // 操作按钮
    }
  },
  filters: { afterSaleStatusFilter },
  props: {
    tableData: {
      type: Array,
      default: () => []
    },
    total: {
      type: Number,
      default: 0
    },
    orderTab: {
      type: String,
      default: ''
    },
    pageSize: {
      type: Number,
      default: 10
    },
    currentPage: {
      type: Number,
      default: 10
    }
  },
  components: {
    MyTable,
    ApplyRefund,
    CloseTrade
  },
  computed: {
    refundReasons() {
      return [
        { key: 'misdiagnose', value: '误诊' },
        { key: 'user_asked', value: '用户要求退款' },
        { key: 'other', value: '其它' }
      ]
    },
    orderTabName() {
      return this.orderTab === 'InquiryServiceType'
        ? '单次问诊'
        : this.orderTab === 'FdServiceType'
        ? '家庭医生'
        : '所有'
    }
  },
  created() {
    this.fieldInit()
  },
  methods: {
    fieldInit() {
      // 获取字段
      var column = orderListTableColumn(this.orderTab)
      column.forEach(item => {
        if (!!item.width && item.width != 'auto') {
          item.width = '300'
        }
      })
      this.column = column
    },
    seeRefundDetail(orderRefundId) {
      this.$router.push({
        name: 'RefundDetail',
        params: { id: orderRefundId }
      })
    },
    applyRefundAfter(userId) {
      localSave(
        'refund-filter-query',
        JSON.stringify({
          searchParams: {
            userId: userId
          },
          disabled: false
        })
      )
      this.$router.push({ name: 'RefundOrderList' })
    },
    seeUserOrder(userId) {
      const sparams = {
        current_page: 1,
        searchParams: {
          user_id: userId,
          order_id: '',
          createdDateRange: '',
          order_type: '',
          order_name: [],
          order_status: '',
          nickname: '',
          product_type: '',
          fd_status: '',
          weapp_scene: '',
          custom_scene: '',
          order_action: '',
          fd_users: '',
          single_ask_department: '',
          single_ask_users: ''
        }
      }
      this.search('', '', sparams)
    },
    handlerRefundButtonClick(row) {
      this.selectOrder = row
      this.dialogVisible = true
    },
    seeDetail(id) {
      this.$router.push({ name: 'OrderDetail', params: { id } })
    },
    handleSizeChange(val) {
      this.search(1, val)
    },
    handleCurrentChange(index) {
      this.search(index, this.pageSize)
    },
    search(current_page, page_size, searchParams) {
      this.$emit('search', {
        current_page: current_page,
        page_size: page_size,
        searchParams: searchParams
      })
    }
  }
}
</script>
<style lang="scss" scope>
.cherry {
  color: #f8747f;
  &:hover {
    color: #f8747f;
  }
}

.trade_detail_btn {
  padding-top: 5px;
  padding-bottom: 0;
}
</style>
