/* eslint-disable prettier/prettier */
export function setFormInfo(tabVal) {
  let showAll = tabVal !=='FdServiceType' && tabVal !== 'InquiryServiceType'
  let showFd = tabVal==='FdServiceType'
  let isSingleAsk = tabVal === 'InquiryServiceType'
  let form = {
    item: [
      {
        type: 'text',
        name: 'order_id',
        value: '',
        width: '100%',
        label: '订单号：',
        placeholder: '请输入订单号',
        show: true
      },
      {
        type: 'datetimerange',
        name: 'createdDateRange',
        value: '',
        width: '100%',
        label: '创建时间：',
        colSpan: 7,
        show: true
      },
      {
        type: 'orderType',
        value: '',
        name: 'order_type',
        width: '100%',
        label: '订单类型',
        show: showAll
      },
      {
        type: 'orderName',
        value: '',
        name: 'order_name',
        orderType: tabVal,
        width: '100%',
        label: '订单名称',
        colSpan: 6,
        show: true
      },
      {
        type: 'orderStatus',
        value: '',
        name: 'order_status',
        width: '100%',
        label: '订单状态',
        show: true
      },
      {
        type: 'text',
        name: 'user_id',
        value: '',
        width: '100%',
        label: '用户ID：',
        placeholder: '请输入用户ID',
        show: true
      },
      {
        type: 'text',
        name: 'nickname',
        value: '',
        width: '100%',
        label: '用户昵称：',
        placeholder: '请输入用户昵称',
        show: true
      },
      {
        type: 'servicePackage',
        value: '',
        name: 'product_type',
        width: '100%',
        label: '套餐类型：',
        placeholder: '请选择套餐类型',
        show: showFd
      },
      {
        type: 'signType',
        value: '',
        name: 'order_action',
        width: '100%',
        label: '签约类型：',
        placeholder: '请选择签约类型',
        show: showFd
      },
      {
        type: 'fdStatus',
        value: '',
        name: 'fd_status',
        width: '100%',
        label: '服务状态：',
        placeholder: '请选择服务状态',
        show: showFd
      },
      {
        type: 'servicePeople',
        value: '',
        width: '100%',
        name: 'fd_users',
        label: '服务人员：',
        colSpan: 6,
        placeholder: '请选择服务人员',
        show: showFd
      },
      {
        type: 'serviceDepartment',
        value: '',
        width: '100%',
        name: 'single_ask_department',
        label: '服务科室：',
        colSpan: 4,
        placeholder: '请选择服务科室',
        show: isSingleAsk
      },
      {
        type: 'servicePeople',
        value: '',
        width: '100%',
        label: '接诊人：',
        placeholder: '请选择接诊人',
        name: 'single_ask_users',
        colSpan: 6,
        peopleType: 'singleAsk',
        show: isSingleAsk
      },
      {
        label: '来源：',
        placeholder: '请选择场景值ID',
        name: 'weapp_scene',
        type: 'weappScene',
        colSpan: 4,
        show: showAll
      },
      {
        label: '',
        placeholder: '请选择场景自定义值',
        name: 'custom_scene',
        labelWidth: '25px',
        type: 'customScene',
        colSpan: 3,
        show: showAll
      },
      { show: true, 
        labelWidth: '0px',
        type: 'btn',
        colSpan: 4
      }
    ],
    data: {
      order_id: '',
      createdDateRange: '',
      order_type: '',
      order_name: '',
      order_status: '',
      user_id: '',
      nickname: '',
      product_type: '',
      fd_status: '',
      weapp_scene: '',
      custom_scene: '',
      order_action: '',
      fd_users: '',
      single_ask_department: '',
      single_ask_users: ''
    }
  }
  return form
}
