<template>
  <div>
    <MyForm
      :form="form"
      ref="orderSearchForm"
      :formData="formData"
      :formItem="formItem"
      :getOrderOptsData="true"
      @resetForm="resetForm"
      @submit="searchSubmit"
      @orderBasicOpts="getOrderBasicOpts"
      @exportFun="exportConfirm"
    >
      <div name="scene">
        来源
      </div>
    </MyForm>
    <Dialog
      :title="'数据导出确认'"
      v-model="dialogVisible"
      :showCancelBtn="true"
      :btnText="'确认'"
      :width="'400px'"
      @btnHandle="exportData"
    >
      <template slot="content">
        <div class="align-center">
          确认导出当前搜索条件的数据吗？
          <p class="fontsize-12 color-orange margin-top-10">
            注：当数据量超过1万条时将无法导出！
          </p>
        </div>
      </template>
    </Dialog>
  </div>
</template>
<script>
import { checkExport } from '@/api/order'
import { mapGetters } from 'vuex'
import { setFormInfo } from '../../formData.js'
import MyForm from '@/components/MyForm'
import Dialog from '@/components/Dialog'
export default {
  data() {
    // 表单配置
    var orderSearchForm = {
      showFormBorder: false,
      showTitle: false,
      ref: 'orderSearchForm',
      labelWidth: '100px',
      labelPositon: 'right',
      style: 'notprepend',
      showExportBtn: true,
      column: 4,
      btnStyle: 'inline',
      hasSubmit: true,
      btnRight: true,
      cancleShow: true,
      submitText: '搜索',
      cancleText: '重置'
    }
    return {
      dialogVisible: false,
      searchParams: {},
      formItem: [],
      formData: {},
      form: orderSearchForm,
      initData: {}
    }
  },

  props: {
    orderTab: {
      type: String,
      default: ''
    },
    moduleName: {
      type: String,
      default: ''
    },
    localFormData: {
      type: Object,
      default: () => {}
    },
    curOrderStatus: {
      type: String,
      default: ''
    }
  },
  components: {
    MyForm,
    Dialog
  },
  watch: {
    localFormData(val) {
      this.formData = Object.assign(this.formData, val.searchParams)
    },
    orderTab() {
      this.resetForm()
    },
    curOrderStatus(val) {
      this.formData.order_status = val
    }
  },
  computed: {
    ...mapGetters(['token'])
  },
  created() {
    this.formInit()
  },
  methods: {
    initDataFun() {
      this.initData = {
        order_id: '',
        createdDateRange: '',
        order_type: '',
        order_name: [],
        order_status: '',
        user_id: '',
        nickname: '',
        product_type: '',
        fd_status: '',
        weapp_scene: '',
        custom_scene: '',
        order_action: '',
        fd_users: '',
        single_ask_department: '',
        single_ask_users: ''
      }
    },
    getOrderBasicOpts(val) {
      this.$emit('orderBasicOpts', val)
    },
    async exportData() {
      if (
        this.formData.createdDateRange &&
        this.formData.createdDateRange.length >= 2
      ) {
        this.formData.create_time_begin = this.formData.createdDateRange[0]
        this.formData.create_time_end = this.formData.createdDateRange[1]
      } else {
        this.formData.create_time_begin = ''
        this.formData.create_time_end = ''
      }
      if (this.orderTab && this.orderTab !== '0') {
        this.formData.order_type = this.orderTab
      } else {
        this.formData.order_type = ''
      }
      let { data } = await checkExport(this.formData)
      if (data.count > 10000) {
        this.$message({
          type: 'warning',
          message: '导出数据大于10000条，无法导出!'
        })
        return
      }
      if (data.count === 0) {
        this.$message({
          type: 'warning',
          message: '导出数据为0条，无法导出!'
        })
        return
      }
      const baseURL = process.env.VUE_APP_BASE_API
      const params = JSON.stringify(this.formData)
      const url =
        baseURL + 'order/data_export?str=' + params + '&token=' + this.token
      window.open(url)
      this.dialogHide()
    },
    dialogHide() {
      this.dialogVisible = false
    },
    exportConfirm() {
      this.dialogVisible = true
    },
    searchSubmit(val) {
      this.resetForm('search')
    },
    resetForm(type) {
      if (type !== 'search') {
        this.initDataFun()
        this.formInit(this.initData)
      }
      const params = {
        searchParams: this.formData,
        current_page: 1,
        page_size: 10
      }
      this.$emit('search', params)
    },
    formInit(row) {
      this.formItem = setFormInfo(this.orderTab).item
      this.formData = row ? row : setFormInfo(this.orderTab).data
    }
  }
}
</script>
