<template>
  <div class="apply-refund">
    <slot />
    <el-dialog
      class="apply-refund__dialog"
      :visible.sync="curDialogVisible"
      append-to-body
      width="400px"
      top="25vh"
      @closed="resetCom"
    >
      <div class="apply-refund__dialog__body">
        <div class="apply-refund__dialog__body__tips">
          <i class="el-icon-warning" />
          <span v-if="orderType === '问诊'"
            >发起主动退款成功后，将直接向微信发起转账申请！请熟知！</span
          >
          <span v-else
            >发起主动退款后，将为该用户所有可退订单发起退款申请！<br />在退款详情页可以进行每笔退款申请的处理！请知悉！</span
          >
        </div>
        <div class="apply-refund__dialog__content">
          <div class="apply-refund__dialog__content__close-txt">
            请选择退款给用户的理由
          </div>
          <el-select
            v-model="refundReason"
            class="apply-refund__dialog__content__select"
            placeholder="请选择退款给用户的理由"
            size="small"
          >
            <el-option
              v-for="{ key, value } in refundReasons"
              :key="key"
              :value="key"
              :label="value"
            />
          </el-select>
          <el-input
            class="margin-top-10"
            v-if="refundReason === 'other'"
            type="text"
            v-model.trim="refundRemark"
            maxlength="10"
            show-word-limit
            placeholder="请填写具体的其他原因"
          ></el-input>
        </div>
        <div
          v-if="orderType !== '问诊'"
          class="apply-refund__dialog__body__down-tips"
        >
          <span
            >单击申请退款后，将为您跳转至退款管理页，可查看该用户关联的所有退款订单
          </span>
        </div>
      </div>
      <span slot="footer" class="apply-refund__dialog__footer">
        <el-button type="primary" size="small" @click="handleSummitRefundReason"
          >申请退款</el-button
        >
        <el-button
          class="apply-refund__dialog__cancel"
          size="small"
          @click="curDialogVisible = false"
          >取消并返回</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { applyRefund as applyRefundApi } from '@/api/order'

import { statusUpdateMessage } from '@/utils/statusUpdateMessage'

export default {
  model: {
    prop: 'dialogVisible',
    event: 'change'
  },
  props: {
    id: {
      type: String,
      required: true
    },
    dialogVisible: {
      type: Boolean,
      default: false,
      required: true
    },
    refundReasons: {
      type: Array,
      default: () => [
        { key: 'misdiagnose', value: '误诊' },
        { key: 'user_asked', value: '用户要求退款' },
        { key: 'other', value: '其它' }
      ]
    },
    orderType: {
      type: String,
      default: () => ''
    }
  },
  data() {
    return {
      curDialogVisible: false,
      refundReason: '',
      refundRemark: ''
    }
  },
  watch: {
    dialogVisible(value) {
      if (value !== this.curDialogVisible) this.curDialogVisible = value
    },
    curDialogVisible(value) {
      this.$emit('change', value)
    }
  },
  methods: {
    handleApplyRefund(id, refundReasonKey, refundRemark) {
      return new Promise(resolve => {
        applyRefundApi(id, {
          refund_reason: refundReasonKey,
          other_reason_message: refundRemark
        }).then(res => {
          statusUpdateMessage(res)
          resolve()
        })
      })
    },
    async handleSummitRefundReason() {
      if (!this.refundReason) {
        this.$message({
          message: '请选择退款给用户的理由',
          type: 'warning'
        })
        return
      }
      if (this.refundReason === 'other' && !this.refundRemark) {
        this.$message({
          message: '请填写具体的其他原因',
          type: 'warning'
        })
        return
      }
      await this.handleApplyRefund(
        this.id,
        this.refundReason,
        this.refundRemark
      ).then(async () => {
        this.curDialogVisible = false
        this.$emit('applyRefundAfter')
      })
    },
    resetCom() {
      this.refundReason = ''
    }
  }
}
</script>

<style lang="scss">
.apply-refund {
  &__dialog {
    .el-dialog {
      box-shadow: none;
      &__header {
        display: none;
      }
      &__body {
        padding: 20px 30px 30px 30px;
      }
      &__footer {
        text-align: center;
      }
    }
    .el-button + &__cancel {
      margin-left: 20px;
      color: #999;
    }
  }
}
</style>

<style lang="scss" scoped>
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';

.apply-refund {
  &__dialog {
    &__body {
      @include ff-regular;
      &__tips {
        display: flex;
        justify-content: flex-start;
        margin-left: -26px;
        width: 100%;
        line-height: 20px;
        font-size: 12px;
        color: #f0717b;
        margin-bottom: 16px;
        transform: scale(0.84);
        i {
          margin-right: 8px;
          height: 20px;
          line-height: 20px;
        }
        span {
          min-width: 380px;
          flex-grow: 1;
          word-break: break-all;
          word-wrap: break-word;
        }
      }
      &__down-tips {
        margin-left: -30px;
        padding-top: 10px;
        min-width: 410px;
        line-height: 20px;
        font-size: 12px;
        color: #666;
        transform: scale(0.84);
      }
    }
    &__content {
      &__select {
        display: block;
      }
      &__close-txt {
        margin-right: -36px;
        color: #{$textDeepBlock};
        margin-bottom: 14px;
      }
    }
  }
}
</style>
